* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;

}
.book-form {
    width: 83vw;
    display: flex;
    align-items: center;
    justify-content: center;

    .background-book {
        height: 90%;
        width: 45%;
        background-image: url("../../../public/addbook.jpg");
        background-position: center;
        background-size: cover;
        border-right: 2px solid white;
    }

    .add-book-section {
        height: 90%;
        width: 45%;
        background-color: #f7f7f7;


        .form-heading {
            margin-top: 30px;
            margin-bottom: 15px;
            margin-left: 48px;
            border-bottom: 2px solid #1677ff;
            width: fit-content;
            font-size: 3rem;
        }

        form {
            padding: 10px 10px;
            display: flex;
            justify-content: start;
            flex-direction: column;

            .form-label {
                font-size: 1.6rem;
                margin-left: 36px;
            }

            input {
                height: 30px;
                width: 80%;
                font-size: 1.5rem;
                border-color: lightgray;
                margin-top: 10px;
                margin-left: 35px;
                border: none;
                border-bottom: 1px solid lightgray;

            }

            .input-file {
                width: 200px;

            }

            input:hover {
                border-color: #4fc3f7;
            }



            span {
                height: 10px;
                font-size: 1.5rem;
                color: red;
                margin-left: 35px;
            }

            .add-book-submit-button {
                width: 80px;
                margin-left: 35px;
                margin-top: 15px;

                span {
                    color: white;
                    margin-left: 0px;

                }
            }

            .preview-image {
                height: 100px;
                width: 100px;
                margin-left: 35px;
                margin-bottom: 15px;

                img {
                    height: 100%;
                    width: 100%;
                }
            }

        }

    }

}

@media (max-width:1290px) {
    .book-form {
        .background-book {
            width: 350px;
            background-position: 70%;

        }

        .add-book-section {
            width: 350px;
        }

    }
}


@media (max-width:1130px) {
    .book-form {
        .background-book {
            width: 350px;
            background-position: 70%;
        }

        .add-book-section {
            width: 320px;

            form {
                width: 320px;
            }
        }
    }
}

@media (max-width:960px) {
    .book-form {
        .background-book {
            display: none;
        }

        .add-book-section {
            width: 500px;

            form {
                width: 500px;
            }

        }

    }
}


@media (max-width:769px) {
    .book-form {

        .add-book-section {
            width: 400px;

            form {
                width: 400px;
            }
        }
    }
}

@media (max-width:700px) {

    .book-form {

        width: 98vw;

        .add-book-section {
            height: 100%;
            width: 500px;

            form {
                width: 500px;
            }
        }
    }
}

@media (max-width:533px) {
    .book-form {
        .add-book-section {
            width: 300px;
            form {
                width: 300px;
            }
        }
    }
}