* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.form-container {
  height: 100vh;
  width: 100vw;
  background-image: url("../../../public/libraryloginpagebg.avif");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}
.form-container .form-section {
  height: 100%;
  max-width: 1920px;
  margin: 0 auto;
  display: flex;
  align-items: center;
  flex-direction: column;
}
.form-container .my-form {
  height: 400px;
  width: 500px;
  background-color: rgba(0, 0, 0, 0.8);
  color: aliceblue;
  box-shadow: 0px 0px 20px black;
  font-size: 18px;
  padding: 10px;
  margin-top: 30px;
}
.form-container .my-form p {
  margin-top: 30px;
  font-size: 25px;
  margin-left: 38px;
}
.form-container .my-form .input-name {
  background: transparent;
  border: none;
  border-bottom: 1px solid white;
  height: 50px;
  width: 80%;
  padding: 10px;
  margin-top: 20px;
  color: white;
  font-size: 15px;
  margin-left: 35px;
}
.form-container .my-form span {
  font-size: 1.5rem;
  margin-left: 36px;
  color: red;
}
.form-container .my-form .form-btn {
  background: transparent;
  border: 1px solid rgb(198, 193, 193);
  font-size: 15px;
  color: white;
  padding: 10px 30px;
  margin-top: 25px;
  margin-left: 36px;
  cursor: pointer;
}
.form-container .my-form .form-btn:hover {
  color: black;
  background-color: rgb(181, 181, 181);
}
.form-container h1 {
  color: aliceblue;
  font-size: 40px;
  margin-top: 30px;
}

@media (max-width: 650px) {
  .form-container .form-section h1 {
    font-size: 30px;
  }
  .form-container .form-section .my-form {
    width: 400px;
  }
}
@media (max-width: 450px) {
  .form-container .form-section .my-form {
    margin-top: 35px;
    width: 300px;
  }
}/*# sourceMappingURL=Login.css.map */